"use client";
import { useMemo, useState } from "react";
import { MapPin, User, Package, ArrowUpDown } from "lucide-react";
import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";
import { unitToKg } from "@/lib/UnitToKg";
import { Sale } from "./Sale";

interface Props {
    sales: Sale[];
}

type View = "location" | "customer" | "product";
type SortKey =
    | "name"
    | "saleCount"
    | "quantity"
    | "totalTonnage"
    | "totalSalesValue"
    | "avgOrderValue"
    | "contribution";

export default function SalesTable({ sales }: Props) {
    const [view, setView] = useState<View>("location");
    const [sortKey, setSortKey] = useState<SortKey>("totalSalesValue");
    const [sortDir, setSortDir] = useState<"asc" | "desc">("desc");
    const [startDate, setStartDate] = useState<Date | null>(null);
    const [endDate, setEndDate] = useState<Date | null>(null);

    const [selectedLocation, setSelectedLocation] = useState<string[]>([]);
    const [selectedCategory, setSelectedCategory] = useState<string[]>([]);
    const [selectedProduct, setSelectedProduct] = useState<string[]>([]);

    const getMultiValues = (e: React.ChangeEvent<HTMLSelectElement>) =>
        Array.from(e.target.selectedOptions, o => o.value);

    const toggleSort = (key: SortKey) => {
        if (key === sortKey) {
            setSortDir(sortDir === "asc" ? "desc" : "asc");
        } else {
            setSortKey(key);
            setSortDir("desc");
        }
    };

    const Header = ({ label, column, align = "left" }: any) => (
        <th
            onClick={() => toggleSort(column)}
            className={`py-2 px-3 cursor-pointer border-r ${align === "right" ? "text-right" : "text-left"
                }`}
        >
            <span className="inline-flex items-center gap-1">
                {label}
                <ArrowUpDown className="w-3 h-3 opacity-60" />
            </span>
        </th>
    );

    const locations = useMemo(
        () =>
            Array.from(
                new Map(
                    sales
                        .filter(s => s.location)
                        .map(s => [s.locationId, s.location])
                ).values()
            ),
        [sales]
    );

    const categories = useMemo(() => {
        const set = new Set<string>();
        sales.forEach(s => s.product?.category && set.add(s.product.category));
        return Array.from(set).sort();
    }, [sales]);

    const products = useMemo(
        () =>
            Array.from(
                new Map(
                    sales
                        .filter(s => s.product)
                        .map(s => [s.product.id, s.product])
                ).values()
            ),
        [sales]
    );

    const filteredSales = useMemo(() => {
        return sales.filter(sale => {
            const saleDate = new Date(sale.saleDate);

            if (startDate && saleDate < startDate) return false;
            if (endDate && saleDate > endDate) return false;

            if (
                selectedLocation.length &&
                !selectedLocation.includes(sale.locationId)
            ) return false;

            if (
                selectedCategory.length &&
                !selectedCategory.includes(sale.product?.category)
            ) return false;

            if (
                selectedProduct.length &&
                !selectedProduct.includes(sale.product.id)
            ) return false;

            return true;
        });
    }, [
        sales,
        startDate,
        endDate,
        selectedLocation,
        selectedCategory,
        selectedProduct,
    ]);

    const aggregatedData = useMemo(() => {
        const map: Record<string, any> = {};

        filteredSales.forEach(sale => {
            let id: string, name: string;

            if (view === "location") {
                id = sale.locationId;
                name = sale.location?.name || "Unknown Location";
            } else if (view === "customer") {
                id = sale.customerId;
                name = sale.customerName || "Unknown Customer";
            } else {
                id = sale.productId;
                name = sale.product?.name || "Unknown Product";
            }

            if (!map[id]) {
                map[id] = {
                    id,
                    name,
                    saleCount: 0,
                    quantity: 0,
                    totalTonnage: 0,
                    totalSalesValue: 0,
                    avgOrderValue: 0,
                    contribution: 0,
                };
            }

            const salesValue = sale.totalAmount ?? sale.salePrice * sale.quantity;
            const weightKg = unitToKg(
                sale.product?.weightValue || 0,
                sale.product?.weightUnit
            );
            const tonnage =
                (weightKg * sale.quantity * (sale.product?.packSize || 1)) / 1000;

            map[id].saleCount += 1;
            map[id].quantity += sale.quantity;
            map[id].totalSalesValue += salesValue;
            map[id].totalTonnage += tonnage;
        });

        const totalRevenue = Object.values(map).reduce(
            (sum: number, d: any) => sum + d.totalSalesValue,
            0
        );

        return Object.values(map).map((d: any) => ({
            ...d,
            avgOrderValue: d.saleCount
                ? d.totalSalesValue / d.saleCount
                : 0,
            contribution: totalRevenue
                ? (d.totalSalesValue / totalRevenue) * 100
                : 0,
        }));
    }, [filteredSales, view]);

    const sortedData = useMemo(() => {
        return [...aggregatedData].sort((a, b) => {
            const aVal = a[sortKey];
            const bVal = b[sortKey];
            if (typeof aVal === "string") {
                return sortDir === "asc"
                    ? aVal.localeCompare(bVal)
                    : bVal.localeCompare(aVal);
            }
            return sortDir === "asc" ? aVal - bVal : bVal - aVal;
        });
    }, [aggregatedData, sortKey, sortDir]);

    /** ------------------ Render ------------------ */
    return (
        <div className="bg-white p-6 rounded-xl border hover:shadow-md">
            <div className="flex items-center justify-between mb-4">
                <div className="flex gap-2">
                    <button
                        className={`px-3 py-1 rounded border ${view === "location" ? "bg-blue-100" : ""
                            }`}
                        onClick={() => setView("location")}
                    >
                        <MapPin className="inline w-4 h-4 mr-1" /> Location
                    </button>
                    <button
                        className={`px-3 py-1 rounded border ${view === "customer" ? "bg-blue-100" : ""
                            }`}
                        onClick={() => setView("customer")}
                    >
                        <User className="inline w-4 h-4 mr-1" /> Customer
                    </button>
                    <button
                        className={`px-3 py-1 rounded border ${view === "product" ? "bg-blue-100" : ""
                            }`}
                        onClick={() => setView("product")}
                    >
                        <Package className="inline w-4 h-4 mr-1" /> Product
                    </button>
                </div>

                <div className="flex gap-2">
                    <select
                        multiple
                        value={selectedLocation}
                        onChange={e => setSelectedLocation(getMultiValues(e))}
                        className="border px-2 py-1 rounded"
                    >
                        {locations.map(l => (
                            <option key={l.id} value={l.id}>
                                {l.name}
                            </option>
                        ))}
                    </select>

                    <select
                        multiple
                        value={selectedCategory}
                        onChange={e => {
                            setSelectedCategory(getMultiValues(e));
                            setSelectedProduct([]);
                        }}
                        className="border px-2 py-1 rounded"
                    >
                        {categories.map(c => (
                            <option key={c} value={c}>
                                {c}
                            </option>
                        ))}
                    </select>

                    <select
                        multiple
                        value={selectedProduct}
                        onChange={e => setSelectedProduct(getMultiValues(e))}
                        className="border px-2 py-1 rounded"
                    >
                        {products
                            .filter(
                                p =>
                                    !selectedCategory.length ||
                                    selectedCategory.includes(p.category)
                            )
                            .map(p => (
                                <option key={p.id} value={p.id}>
                                    {p.name}
                                </option>
                            ))}
                    </select>
                </div>
            </div>

            <div className="max-h-[420px] overflow-y-auto">
                <table className="w-full text-sm border border-gray-200">
                    <thead className="sticky top-0 bg-gray-200">
                        <tr>
                            <th className="py-2 px-3 border-r text-center">#</th>
                            <Header label="Name" column="name" />
                            <Header label="Orders" column="saleCount" align="right" />
                            <Header label="Quantity" column="quantity" align="right" />
                            <Header label="Tonnage" column="totalTonnage" align="right" />
                            <Header label="Avg Order Value" column="avgOrderValue" align="right" />
                            <Header label="Revenue" column="totalSalesValue" align="right" />
                            <Header label="% Share" column="contribution" align="right" />
                        </tr>
                    </thead>
                    <tbody>
                        {sortedData.length === 0 ? (
                            <tr>
                                <td colSpan={8} className="py-6 text-center text-gray-500 italic">
                                    No data available
                                </td>
                            </tr>
                        ) : (
                            <>
                                {sortedData.map((d, i) => (
                                    <tr key={d.id} className={i % 2 ? "bg-gray-50" : ""}>
                                        <td className="py-2 px-3 border-r text-center">{i + 1}</td>
                                        <td className="py-2 px-3 border-r">{d.name}</td>
                                        <td className="py-2 px-3 border-r text-right">{d.saleCount}</td>
                                        <td className="py-2 px-3 border-r text-right">{d.quantity}</td>
                                        <td className="py-2 px-3 border-r text-right">{d.totalTonnage.toFixed(2)}</td>
                                        <td className="py-2 px-3 border-r text-right">K{d.avgOrderValue.toFixed(0)}</td>
                                        <td className="py-2 px-3 border-r text-right">K{d.totalSalesValue.toFixed(0)}</td>
                                        <td className="py-2 px-3 border-r text-right">{d.contribution.toFixed(1)}%</td>
                                    </tr>
                                ))}
                                <tr className="bg-gray-200 font-bold">
                                    <td className="py-2 px-3 border-r text-center" colSpan={2}>
                                        Total
                                    </td>
                                    <td className="py-2 px-3 border-r text-right">
                                        {sortedData.reduce((sum, d) => sum + d.saleCount, 0)}
                                    </td>
                                    <td className="py-2 px-3 border-r text-right">
                                        {sortedData.reduce((sum, d) => sum + d.quantity, 0)}
                                    </td>
                                    <td className="py-2 px-3 border-r text-right">
                                        {sortedData.reduce((sum, d) => sum + d.totalTonnage, 0).toFixed(2)}
                                    </td>
                                    <td className="py-2 px-3 border-r text-right">
                                        K{(
                                            sortedData.reduce((sum, d) => sum + d.totalSalesValue, 0) /
                                            sortedData.reduce((sum, d) => sum + d.saleCount, 0)
                                        ).toFixed(0)}
                                    </td>
                                    <td className="py-2 px-3 border-r text-right">
                                        K{sortedData.reduce((sum, d) => sum + d.totalSalesValue, 0).toFixed(0)}
                                    </td>
                                    <td className="py-2 px-3 border-r text-right">100%</td>
                                </tr>
                            </>
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    );
}
